import { useSpringCarousel } from "react-spring-carousel";

const Carousel = () => {
  const { carouselFragment } = useSpringCarousel({
    items: [
      {
        id: "item-1",
        renderItem: (
          <div className="flex flex-col gap-4">
            <div className="flex">
              <span className="text-white text-lg font-bold">
                01
                <span className="text-white text-lg text-opacity-50">
                  {" "}
                  / 03
                </span>
              </span>
            </div>
            <h2 className="text-white text-[34px] font-bold">
                
            </h2>
            <p className="text-white text-opacity-75 text-sm tracking-[-0.42px]">
                 :   ,  
               ,    .  ,  
                .      ,
              , -.
            </p>
          </div>
        ),
      },
      {
        id: "item-2",
        renderItem: (
          <div className="flex flex-col gap-4">
            <div className="flex">
              <span className="text-white text-lg font-bold">
                02
                <span className="text-white text-lg text-opacity-50">
                  {" "}
                  / 03
                </span>
              </span>
            </div>
            <h2 className="text-white text-[34px] font-bold">
              -
            </h2>
            <p className="text-white text-opacity-75 text-sm tracking-[-0.42px]">
               -    
               - . SEO- 
                      
              . -   -,
                  .
            </p>
          </div>
        ),
      },
      {
        id: "item-3",
        renderItem: (
          <div className="flex flex-col gap-4">
            <div className="flex">
              <span className="text-white text-lg font-bold">
                03
                <span className="text-white text-lg text-opacity-50">
                  {" "}
                  / 03
                </span>
              </span>
            </div>
            <h2 className="text-white text-[34px] font-bold">
                
            </h2>
            <p className="text-white text-opacity-75 text-sm tracking-[-0.42px]">
                 - . , 
                     
               .     
                  .
            </p>
          </div>
        ),
      },
    ],
  });

  return (
    <div className="container mx-auto flex justify-center">
      <div className="relative overflow-hidden max-w-[320px]">
        {carouselFragment}
      </div>
    </div>
  );
};

export default Carousel;
